import React, { Component } from 'react';

import {
  Container, Form,
  FormGroup, Label, Input, Row,
  Button,
} from 'reactstrap';
import  { Redirect } from 'react-router-dom';
import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../core/services/commonService';
import Loader from '../Loader/Loader';
import ForgotPassword from './ForgotPassword';

import './Login.css';
const logoUrl = `${process.env.PUBLIC_URL}/logo.png`;
const loginUrl = `${process.env.PUBLIC_URL}/images/log-In.jpg`;
const backgroundImageUrl = `${process.env.PUBLIC_URL}/images/site-inspection.jpg`;
class Login extends Component {
  constructor(props) {
    super(props)
  
    this.unsubscribe = null;
      this.state = {
      email: '',
      password: '',
      userName: '',
      forgotPasswordEmail: "",
      modal: false,
      loggedIn: false,
      loading: false,      
      isLoggedIn: false,
      errors: {},
      showforgotPassword: false,
      settingsData: {}
    }

  }

  componentDidMount() { 
    
  }

  

  submitHandler = event => {
    event.preventDefault();
    event.target.className += " was-validated";
    if(!this.validateForm())
      return false;
    this.setState({loading: true}, () => {;
        commonService.postAPI("auth/sign-in", {email: this.state.email, password: this.state.password}).then((res) => {
          if ( undefined === res.data || !res.data.status ) {
            this.setState( { loading: false } );
            toast.error(res.data.message);
            if(!res.data.status) {
              if(res.data.isAccountVerified !== undefined && !res.data.isAccountVerified) {
                this.setState( { loading: true, isLoggedIn: true }, () => {
                  commonService.postAPI( `auth/resend-otp`, {email: this.state.email} )
                    .then( res => {
                     
                      console.log(res);
                      if ( undefined === res.data || !res.data.status ) {
                        this.setState( { loading: false} );
                        toast.error(res.data.message);
                        return;
                      }
                      this.setState( { loading: false } );
                      toast.success(res.data.message);            
                    } )
                    .catch( err => {
                      
                      this.setState( { loading: false } );
                      toast.error(err.message);
                    } )
                } )
              }
            }
            return;
          }
  
          const loggedInfo = res.data;
          if(loggedInfo.data.role.toLowerCase() === "admin") {
            commonService.setLocalStorageValue( 'accessToken', loggedInfo.data.accessToken);
            commonService.setLocalStorageValue( 'refreshToken', loggedInfo.data.refreshToken);
            commonService.setLocalStorageValue( 'role', loggedInfo.data.role);
            commonService.setLocalStorageValue( 'authId', loggedInfo.data.authId);
            localStorage.setItem( 'profilePic', loggedInfo.data.profilePic );
            commonService.setLocalStorageValue( 'userName', loggedInfo.data.firstName );
            
            this.props.history.push("/admin/dashboard")
          }
          else
            toast.error("You have not permission to access dashboard");
        }).catch((err) => {
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.propHistory.push('/login');
          }
          else {
            this.setState( { loading: false, loggedIn: true } );
            toast.error(err.message);    
          }
          
        })
    });
  }
  changeHandler = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  validateForm() {
    let errors = {};
    let formIsValid = true;
    if (!this.state.password) {
        formIsValid = false;
        errors["password"] = "*Please enter password.";
    }
    if (!this.state.email) {
        formIsValid = false;
        errors["email"] = "*Please enter your email.";
    }
    
   
    
    this.setState({
      loading: false,
      errors: errors
    });
    
    return formIsValid;
  }
  showForgotPasswordForm(){
    this.setState({showforgotPassword: true})
  }
  hideForgotPasswordForm(){
    this.setState({showforgotPassword: false})
  }

  render() {
    const { email, password, loggedIn, loading, errors} = this.state;
    if ( loggedIn ) {
      if(commonService.getLocalStorageValue('role') === "admin")
        return ( <Redirect to={`/admin`} noThrow /> )
      else
        return ( <Redirect to={`/dashboard`} noThrow /> )

    }
    return (
      <div className="login-page" style ={{backgroundImage:  `url("${backgroundImageUrl}")`}}>
        <Container className="Login">
        <div className="row">
          <div className="col-md-6">
            <div className="researchImage-L"><img src={loginUrl} alt="logo" /></div>
          </div>
          <div className="col-md-6">
            <div className="login-page-header"><img src={logoUrl} alt="logo" /></div>
            <div className="loginForm">
            <div className="loginForm-1">
              <h2>Sign In</h2>
              {loading ? <Loader /> : <></>}
              <ToastContainer />
              <Form className="form needs-validation" onSubmit={this.submitHandler} noValidate>
                  <FormGroup>
                    <Label>Email</Label>
                    <Input
                      type="email"
                      name="email"
                      invalid={errors['email'] !== undefined && errors['email'] !== ""} value={email} onChange={this.changeHandler} id="email"
                      placeholder="myemail@email.com"
                    />
                    <div className="invalid-feedback">
                      Please enter your registered email-id.
                    </div>
                  </FormGroup>
                  <FormGroup>
                    <Label for="examplePassword">Password</Label>
                    <Input
                      type="password"
                      name="password"
                      invalid={errors['password'] !== undefined && errors['password'] !== ""} value={password} onChange={this.changeHandler} id="password"
                      placeholder="********"
                    />
                    <p className="forgot-text " onClick = {this.showForgotPasswordForm.bind(this)}>Forgot Password?</p>
                    <div className="invalid-feedback">
                      Please enter your registered password.
                    </div>
                  </FormGroup>
                  
                  <Button type="submit" className="submit-btn-login">Submit</Button>
                  <Row>
                  <div className="col-md-6"></div>
                  </Row>
              </Form>
              {this.state.showforgotPassword ? <ForgotPassword hideForgotPasswordForm = {this.hideForgotPasswordForm.bind(this)} /> : ""}
              </div>
            </div>
          </div>
        </div>
        </Container>
      </div>
    );
  }
}

export default Login;